.combined-squares .flex-2 {
  height: 500px;
}
.combined-squares .flex-2:nth-child(2) {
  flex-direction: row-reverse;
}
.combined-squares .flex-2 h2 {
  color: var(--main-color);
  font-size: 50px;
}
.combined-squares .flex-2 article {
  width: 50%;
  padding: 20px;
}
.combined-squares .flex-2 div {
  height: 100%;
  width: 50%;
}
.combined-squares .flex-2 div img {
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.cards {
  margin: 50px 0px;
}
.cards .container {
  justify-content: center;
  gap: 50px;
}

.why-us .container .flex-2 {
  gap: 30px;
}
.why-us .container .flex-2 article {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
}
.why-us .container .flex-2 article > p {
  font-family: "Agbalumo", sans-serif;
  text-align: center;
  grid-column-start: 1;
  grid-column-end: 3;
  font-size: 30px;
}

.services > p {
  text-align: center;
  text-transform: capitalize;
}
.services > p:first-child {
  font-size: 55px;
  font-weight: bold;
}
.services .container {
  padding: 100px 0px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  gap: 30px;
}
.services .container .FeatureTile-comp {
  padding: 12px;
  border: 2px solid var(--main-color);
  border-radius: 2px;
  align-items: flex-start;
}
.services .container .FeatureTile-comp * {
  color: var(--main-color);
}
.services .container .FeatureTile-comp .description {
  border: none;
}
.services .container .FeatureTile-comp .description span {
  font-weight: bold;
  font-size: 30px;
}
.services .container > div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 100px;
}

.testmonials {
  padding: 50px;
}
.testmonials .container header {
  margin-bottom: 150px;
  text-align: center;
}
.testmonials .container header span {
  color: var(--secondary-color);
  font-weight: bold;
  font-size: 20px;
}
.testmonials .container header p {
  font-size: 45px;
  font-weight: bold;
  color: var(--main-color);
}
.testmonials .container .slider {
  display: flex;
  gap: 80px;
}
.testmonials .container .slider .testmonialCard-comp {
  position: relative;
  box-shadow: 0px 0px 20px 5px lightgray;
  padding: 100px 20px 20px;
}
.testmonials .container .slider .testmonialCard-comp > img {
  position: absolute;
  top: 0px;
  left: 20px;
  transform: translateY(-50%);
  width: 150px;
  border: 2px solid gray;
}
.testmonials .container .slider .testmonialCard-comp .hash {
  position: absolute;
  right: 0%;
  top: -45%;
  font-size: 200px;
  font-weight: bold;
  color: rgba(52, 152, 219, 0.5);
  transform: rotate(10deg);
}
.testmonials .container .slider .testmonialCard-comp .content p {
  color: var(--main-color);
}
.testmonials .container .slider .testmonialCard-comp .content .doctor-name {
  color: var(--secondary-color);
  font-weight: bold;
  font-size: 30px;
}/*# sourceMappingURL=homepage.css.map */