.navbar-comp {
  background-color: var(--main-color);
  position: fixed;
  width: 100%;
  top: 0px;
  padding: 10px 0px;
}
.navbar-comp .container ul * {
  color: white;
}
.navbar-comp .container ul .logo {
  font-size: 30px;
  font-weight: bold;
  text-transform: capitalize;
  font-family: "Agbalumo", sans-serif;
}
.navbar-comp .container ul ul {
  gap: 20px;
}
.navbar-comp .container ul ul a {
  text-transform: capitalize;
  padding: 2px;
  text-decoration: none;
  position: relative;
}
.navbar-comp .container ul ul a::before {
  content: "";
  position: absolute;
  width: 0%;
  bottom: 0px;
  left: 0px;
  height: 2px;
  background-color: var(--background-color);
  transition: width 0.4s ease-in;
}
.navbar-comp .container ul ul a:hover::before {
  width: 100%;
}
.navbar-comp .container ul > div {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  gap: 20px;
}
.navbar-comp .container ul > div button {
  color: var(--main-color);
  border: none;
  padding: 8px 4px;
  border-radius: 4px;
  font-weight: bold;
}
.navbar-comp .container ul > div .languages {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100px;
}/*# sourceMappingURL=navbar.css.map */